import dUserbox from "../../components/d-userbox.vue";
import cmform from "../../components/cmform.vue";
var app = require("../../common/common.js");
var id;
export default {
	components: {
		dUserbox,
		cmform
	},
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad: function (option) {
		id = option.id;
		this.getPage();
		this.addClick();
		
	},
	onShareAppMessage:function(){
		
	},
	onPullDownRefresh:function(){
		this.refresh();
	} ,
	methods: {
		refresh:function(){
			this.getPage();
			setTimeout(function(){
				uni.stopPullDownRefresh();
			},1000)
		},
		addClick:function(){
			uni.request({
				url: app.apiHost + "/module.php?fromapp=wxapp&m=b2b_blog&ajax=1&a=addclick&id=" + id,
				success: function (res) {
				}
			})
		},
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "/module.php?fromapp=wxapp&m=b2b_blog&ajax=1&a=show&id=" + id,
				data:{
					authcode:this.app.getAuthCode()
				},
				success: function (res) {
					
					that.pageLoad = true;
					//res.data.data.data.content+='<style>img{max-width:100%;width:220px;height:auto;}</style>';
					res.data.data.data.content=app.html(res.data.data.data.content);
					console.log(res.data.data.data.content);
					that.pageData = res.data.data;

				}
			})
		},
		favToggle:function(id){
			var that=this;
			uni.request({
				url:that.app.apiHost+"?fromapp=wxapp&m=fav&a=toggle&ajax=1",
				data:{
					objectid:id,
					authcode:that.app.getAuthCode(),
					tablename:"mod_b2b_blog"  
				},
				success:function(res){
					if(res.data.error==1000){
						that.app.goLogin();
						return false;
					}
					if(res.data.data=='delete'){
						that.pageData.isfav=false;
					}else{
						that.pageData.isfav=true;
					}
					 
				}
			})
		},
		loveToggle:function(id){
			var that=this;
			uni.request({
				url:that.app.apiHost+"?m=love&a=toggle&ajax=1",
				data:{
					
					fromapp:that.app.fromapp(),
					objectid:id,
					authcode:that.app.getAuthCode(),
					tablename:"mod_b2b_blog"
				},
				success:function(res){
					if(res.data.error==1000){
						that.app.goLogin();
						return false;
					}
					if(res.data.data=='delete'){
						that.pageData.islove=false;
					}else{
						that.pageData.islove=true;
					}
					
				}
			})
		}
		
	},
}
